.container-card__info__list
    margin-top:14px

    @import "./buttonStyle"

    .container-card__info__list-group
        display: flex

        .container-card__info__list-right1
            margin-left: 20px !important

        .container-card__info__list-right
            margin-left: 38px
                        
        .container-card__info__list-left, .container-card__info__list-right
            display: flex
            flex-direction: column 
            align-items: center

            li
                font-family: 'Arial'
                font-style: normal
                font-weight: 400
                font-size: 16px
                line-height: 21px
                text-decoration-line: underline


            &:hover
                color: #00BBEA 
                text-decoration-line: none


            .container-card__info__list-height
                margin-top: 8px
                

        
            img 
                margin: 0px 15px
                
                .container-card__info__list-right-block
                    margin-left: 38px

                .container-card__info__list-right-block1
                    margin-left: 0px
